.step-number {
  position: relative;
  /*width: fit-content;*/
  padding: 10px;
  /*border: 1px solid #FED2D3;*/
  border-radius: 50%;
  margin: 0 auto;
}

.step-number.vertical-step {
  position: relative;
  width: -moz-fit-content;
  width: fit-content;
  padding: 10px;
  border: 1px solid #fed2d3;
  border-radius: 50%;
  margin: 0 auto;
}

.step-number img {
  border-radius: 50%;
  z-index: 10;
  max-width: 100%;
}

.step img {
  max-width: 100%;
}

.img-step-wrap {
  width: 50%;
  height: fit-content;
  position: relative;
}

.img-step-wrap:before {
  border-radius: 50%;
  content: " ";
  background: transparent;
  display: block;
  position: absolute;
  top: -6px;
  left: -6px;
  right: -6px;
  bottom: -6px;
  border: solid 1px #FED2D3;
}

@media (min-width: 576px) {
  .img-step-wrap {
    width: fit-content;
  }

  .img-step-wrap:before {
    top: -12px;
    left: -12px;
    right: -12px;
    bottom: -12px;
  }
}
.step-number:before {
  content: " ";
  font-size: large;
  color: #00FF16;
  background: #FED2D3;
  display: block;
  height: calc(50% - 22px);
  width: 1px;
  position: absolute;
  top: 0;
  left: 50%;
}

.step-number.vertical-step:before {
  content: " ";
  font-size: large;
  color: #00ff16;
  background: #fed2d3;
  display: block;
  height: 1px;
  width: 0;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(-100%);
}

.step-number:after {
  content: " ";
  font-size: large;
  color: #00FF16;
  background: #FED2D3;
  display: block;
  height: calc(50% - 21px);
  width: 1px;
  position: absolute;
  bottom: 0;
  left: 50%;
}

@media (min-width: 576px) {
  .step-number:before {
    height: calc(50% - 5px);
  }

  .step-number:after {
    height: calc(50% - 45px);
  }
}
@media (min-width: 768px) {
  .step-number:before {
    height: calc(50% - 13px);
  }

  .step-number:after {
    height: calc(50% - 51px);
  }
}
@media (min-width: 992px) {
  .step-number:before {
    height: calc(50% - 26px);
  }

  .step-number:after {
    height: calc(50% - 65px);
  }

  .step-number.vertical-step:before {
    width: 230px;
  }
}
@media (min-width: 1200px) {
  .step-number.vertical-step:before {
    width: 290px;
  }
}
.step-number.step-1:before {
  display: none;
}

.step-number.vertical-step.no-line:before {
  display: none;
}

.step-number.step-3:after {
  display: none;
}

.step-number.step-blank:after {
  display: none;
}

.step-number.step-blank:before {
  content: " ";
  font-size: large;
  color: #00FF16;
  background: #FED2D3;
  display: block;
  height: 100%;
  width: 1px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 50%;
}

