.grad-bg {
  background: #000000;
  background: -moz-radial-gradient(75% 25%, ellipse cover, #4b4b4b 0%, #000000 50%);
  /* FF3.6-15 */
  background: -webkit-radial-gradient(75% 25%, ellipse cover, #4b4b4b 0%, #000000 50%);
  /* Chrome10-25,Safari5.1-6 */
  background: radial-gradient(ellipse at 75% 25%, #4b4b4b 0%, #000000 50%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#4b4b4b", endColorstr="#000000",GradientType=1 );
  /* IE6-9 fallback on horizontal gradient */
  background-position: 75% 25%;
  color: #FFFFFF;
}

