/** header **/
.header {
    background-color: $bck-color-2; 
    background: -webkit-gradient(linear,left top,left bottom,from(#323232),to(#181818));
    background: linear-gradient(180deg,#323232 0,#181818);
    color: #fff;
    text-align: center;
    position: fixed;
    z-index: 10;
    top: 0;
    left: 0;
    right: 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-align: space-between;
    -ms-flex-align: space-between;
    align-items: space-between;
    border-bottom: 0px solid #b01d20;
    -ms-flex-line-pack: justify;
    align-content: space-between;
    min-height: 70px;
}

